import { model, Schema, models } from 'mongoose';

export default function gethologramsModel() {
  const collectionName = 'holograms';
  if (models[collectionName]) {
    return model(collectionName);
  }
  const hologramsSchema = new Schema({
    spoolBarCode: {
      type: String,
      required: true,
    },
    healStartCode: {
      type: String,
      required: true,
    },
    spoolEndCode: {
      type: String,
      required: true,
    },
    purpose: {
      type: String,
      required: true,
    },
    quantity: {
      type: Number,
      required: true,
    },
  });

  return model(collectionName, hologramsSchema);
}
